// @flow
import React, { Fragment } from 'react';
import _get from 'lodash/get';
import { makeStyles } from '@material-ui/core/styles';
import type { Element } from 'react';
import { Widget } from 'components/Widget';
import type { Offer as OfferType } from 'types/Offer';
import _isEmpty from 'lodash/isEmpty';
import Step from './Step';
import { getStepProps } from './utils/helpers';
import { STEPS } from './utils/constants';

const useStyles = makeStyles(theme => ({
  widget: {
    marginBottom: theme.spacing(3),
  },
  address: {
    ...theme.typography.h5,
    marginBottom: theme.spacing(1),
    color: theme.palette.primary.gray,
  },
  message: {
    ...theme.typography.h5,
    color: theme.palette.common.black,
    marginBottom: theme.spacing(3),
  },
}));

type Props = {
  offer: OfferType,
  openOfferDialogHandler: Function,
  openConfirmTransferHandler: Function,
  openScheduleInspectionHandler: Function,
  openOriginalOfferDialogHandler: Function,
};

const Offer = ({
  offer = {},
  openOfferDialogHandler,
  openConfirmTransferHandler,
  openScheduleInspectionHandler,
  openOriginalOfferDialogHandler,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();

  if (_isEmpty(offer)) return <></>;

  return (
    <Widget className={classes.widget} title="Home Purchase Action Steps">
      <div className={classes.address}>
        <strong>{_get(offer, 'Property.PropertyData.FullAddress', '')}</strong>
      </div>
      <div className={classes.message}>
        {_get(offer, 'Step.messages.buyer', '')}
      </div>
      {STEPS.map(step => {
        const { stepId, ...props } = getStepProps({
          offer,
          step,
          openOfferDialogHandler,
          openConfirmTransferHandler,
          openScheduleInspectionHandler,
          openOriginalOfferDialogHandler,
        });
        return <Step {...props} key={stepId} />;
      })}
    </Widget>
  );
};

export default Offer;
