// @flow
import _get from 'lodash/get';
import type { Offer as OfferType } from 'types/Offer';
import _isEmpty from 'lodash/isEmpty';
import { VALID_STEPS } from './constants';
import { getStepAction } from './stepActionHelpers';
import type { handlersType } from './stepActionHelpers';

type isEnabledStepParams = {
  offerStep: number,
  contingencies: Array<Object>,
  stepId: number,
};

type isCancellStepParams = {
  offerStep: number,
  stepId: number,
};

type getStepPropsParams = {
  offer: OfferType,
  step: Object,
} & handlersType;

const isEnabledStep = ({
  offerStep,
  contingencies,
  stepId,
}: isEnabledStepParams): boolean => {
  const isPleaseCompleteEnabled =
    stepId === VALID_STEPS.pleaseComplete &&
    offerStep === VALID_STEPS.pleaseResubmit; // Combine step 1 with step 150

  const isWireEnabled =
    stepId === VALID_STEPS.confirmTransfer &&
    offerStep >= VALID_STEPS.confirmTransfer;

  const isViewOriginalOfferEnabled =
    stepId === VALID_STEPS.viewOriginalOffer &&
    offerStep > VALID_STEPS.pleaseComplete;

  const isContingencyRemovalEnabled =
    stepId === VALID_STEPS.contingencyRemoval &&
    offerStep >= VALID_STEPS.confirmTransfer &&
    !(
      isCancelledStep({ offerStep, stepId }) ||
      _isEmpty(contingencies) ||
      _get(contingencies, 'AllRemoved')
    );

  return (
    isPleaseCompleteEnabled ||
    isWireEnabled ||
    isViewOriginalOfferEnabled ||
    isContingencyRemovalEnabled ||
    offerStep === stepId
  );
};

const isFinishedStep = ({
  offerStep,
  contingencies,
  stepId,
}: isEnabledStepParams): boolean => {
  if (stepId === VALID_STEPS.pleaseComplete) {
    return (
      offerStep >= VALID_STEPS.awaitingAdvisorApproval &&
      offerStep !== VALID_STEPS.pleaseResubmit
    ); // Combine step 100 with step 150
  }

  if (stepId === VALID_STEPS.viewOriginalOffer)
    return offerStep >= VALID_STEPS.awaitingAdvisorApproval;

  if (stepId === VALID_STEPS.contingencyRemoval)
    return _get(contingencies, 'AllRemoved');

  return stepId < offerStep;
};

const isCancelledStep = ({
  offerStep,
  stepId,
}: isCancellStepParams): boolean => {
  return (
    [
      VALID_STEPS.advisorDecline,
      VALID_STEPS.sellerDecline,
      VALID_STEPS.buyerDecline,
    ].includes(offerStep) && stepId >= VALID_STEPS.buyerNeedToReviewNewOffer
  );
};

export const getStepProps = ({
  offer,
  step,
  openOfferDialogHandler,
  openConfirmTransferHandler,
  openScheduleInspectionHandler,
  openOriginalOfferDialogHandler,
}: getStepPropsParams): Object => {
  const offerStep = _get(offer, 'StepId', 0);
  const contingencies = _get(offer, 'Contingencies');
  const stepId = _get(step, 'id', 0);
  const isCancelled = isCancelledStep({ offerStep, stepId });
  const isEnabled = isEnabledStep({ offerStep, contingencies, stepId });
  const isFinished = isFinishedStep({ offerStep, contingencies, stepId });
  const action =
    !isCancelled && isEnabled
      ? getStepAction({
          offer,
          stepId,
          openOfferDialogHandler,
          openConfirmTransferHandler,
          openScheduleInspectionHandler,
          openOriginalOfferDialogHandler,
        })
      : {};

  return {
    offer,
    step,
    stepId,
    isCancelled,
    isEnabled,
    isFinished,
    action,
  };
};

export const hideWireConfirmButton = (offer: OfferType): boolean => {
  const offerStep = _get(offer, 'StepId', 0);
  return offerStep > VALID_STEPS.confirmTransfer;
};
