// @flow
import type { Element } from 'react';
import React from 'react';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { Divider, Grid, makeStyles } from '@material-ui/core';
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from 'components/Dialog';
import { formatPrice } from '../../../helpers/number';
import { ViewOfferActionButtons } from './ViewOfferActionButtons';
import {
  displayFurniture,
  getBuyerName,
  getFullName,
  displayRentBack,
} from '../../../helpers/Offer';
import { FUNDING_PURCHASE_TYPES } from '../../../shared/Consts';
import { useLockBodyScroll } from '../../../hooks';

const useStyles = makeStyles(theme => ({
  spacing: {
    padding: theme.spacing(2),
  },
  title: {
    fontFamily: theme.typography.fontFamily.avenir85,
  },
  dialogContent: {
    padding: theme.spacing(2),
    paddingTop: 0,
  },
  field: {
    marginBottom: theme.spacing(1.5),
  },
  fieldLabel: {
    marginBottom: theme.spacing(1),
    color: theme.palette.primary.gray,
    textTransform: 'uppercase',
  },
  fieldValue: {
    color: theme.palette.text.secondaryDark,
  },
}));

type Props = {
  open: boolean,
  onClose: Function,
  offer?: Object,
  handleApproval: Function,
  handleDecline: Function,
  handleCounter: Function,
  isSubmitting: boolean,
};

export const ViewOfferDialog = (props: Props): Element<typeof Dialog> => {
  const {
    open,
    onClose,
    offer,
    handleApproval,
    handleDecline,
    handleCounter,
    isSubmitting,
  } = props;

  const classes = useStyles();

  useLockBodyScroll({ open });

  const funding = _get(offer, 'Funding.PurchaseType', '');
  const initialRentBack = _get(
    offer,
    'MappingCounter.RentBackInititated',
    false,
  );

  const fields = [
    { label: 'Offer No', value: _get(offer, 'OfferNo', '') },
    {
      label: 'Down Payment',
      value: formatPrice(_get(offer, 'Funding.DownPayment', 0)),
    },
    {
      label: 'funding',
      value: funding,
    },
    {
      label: 'asking price',
      value: _get(offer, 'Property.ListingDetails.ListPrice')
        ? formatPrice(_get(offer, 'Property.ListingDetails.ListPrice'))
        : 'TBD',
    },
    {
      label: 'offer price',
      value: _get(offer, 'MappingCounter.OfferPrice')
        ? formatPrice(_get(offer, 'MappingCounter.OfferPrice'))
        : 'TBD',
    },
    {
      label: 'finance contingency',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? 'N/A'
          : _get(offer, 'MappingCounter.FinanceContingency', ''),
    },
    {
      label: 'due diligence contingency',
      value: _get(offer, 'MappingCounter.InspectionContingency', ''),
    },
    {
      label: 'home sale contingency',
      value: _get(offer, 'MappingCounter.SaleOfHomeContingency', ''),
    },
    {
      label: 'Closing Period',
      value: _get(offer, 'MappingCounter.ClosingPeriod', ''),
    },
    {
      label: 'Furniture',
      value: displayFurniture(
        _get(offer, 'MappingCounter.Furniture', ''),
        _get(offer, 'MappingCounter.FurnitureOther', ''),
      ),
    },
    { label: 'Buyer Name', value: getBuyerName(offer) },
    {
      label: 'Buyer Agent Name',
      value: getFullName(_get(offer, 'AgentInfo', {})),
    },
    {
      label: 'Nile Listing Agent Name',
      value: 'Khaled Yatim',
    },
    {
      label: 'Property Address',
      value: _get(offer, 'Property.PropertyData.FullAddress', ''),
    },
    ...(initialRentBack
      ? [
          {
            label: 'Rent Back',
            value: displayRentBack(_get(offer, 'MappingCounter', {})),
          },
        ]
      : []),
  ];

  return (
    <Dialog
      open={open}
      aria-labelledby="customized-dialog-title"
      onClose={onClose}
      scroll="paper"
      fullWidth
      maxWidth="sm"
      visible={1}
    >
      <DialogTitle
        id="customized-dialog-title"
        disableTypography
        onClose={onClose}
        classes={{ root: classes.spacing }}
      >
        <Typography variant="h4" className={classes.title}>
          Offer {_get(offer, 'OfferNo', '')}
        </Typography>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          {fields.map(({ label, value }) => (
            <Grid item xs={4} key={label} className={classes.field}>
              <Typography variant="h6" className={classes.fieldLabel}>
                {label}
              </Typography>
              <Typography variant="h5" className={classes.fieldValue}>
                {value}
              </Typography>
            </Grid>
          ))}
        </Grid>
      </DialogContent>
      <Divider />
      <DialogActions>
        <ViewOfferActionButtons
          offer={offer}
          onAccept={handleApproval}
          onDecline={handleDecline}
          onCounter={handleCounter}
          disabled={isSubmitting}
        />
      </DialogActions>
    </Dialog>
  );
};
