// @flow
import * as React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import {
  CancelOutlined,
  CheckCircleOutline,
  RadioButtonUnchecked,
  ArrowForwardIos,
} from '@material-ui/icons';
import { Link } from 'react-router-dom';

type Props = {
  step: Object,
  isEnabled: boolean,
  isFinished: boolean,
  isCancelled: boolean,
  action: Object,
  offer: Object,
};

const useStyles = makeStyles(theme => ({
  link: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.background.lightGray,
    padding: theme.spacing(2.125),
    marginBottom: theme.spacing(1),
    display: 'flex',
    alignItems: 'center',
    cursor: 'pointer',
  },
  name: {
    ...theme.typography.h5,
    marginLeft: theme.spacing(1),
    color: theme.palette.text.dark,
    flex: 1,
    marginTop: '2px',
  },
  linkHighlight: {
    fontWeight: '900',
  },
  disabledLink: {
    pointerEvents: 'none',
    opacity: 0.5,
  },
  selectIcon: {
    float: 'right',
    width: '14px',
    height: '14px',
    color: theme.palette.primary.main,
  },
  checkIcon: {
    color: theme.palette.primary.main,
  },
  disabledIcon: {
    color: theme.palette.primary.gray,
  },
  loopIcon: {
    color: theme.palette.primary.gray,
  },
  cancelIcon: {
    color: theme.palette.error.dark,
  },
}));

const Step = (props: Props): React.Element<'div'> => {
  const { step, isEnabled, isFinished, isCancelled, action, offer } = props;
  const classes = useStyles();

  const isEnabledStep = isEnabled && !isCancelled;
  const { redirectTo, onClickHandler } = action;

  const getIcon = () => {
    if (isCancelled) return <CancelOutlined className={classes.cancelIcon} />;
    if (isFinished) return <CheckCircleOutline className={classes.checkIcon} />;
    return <RadioButtonUnchecked className={classes.loopIcon} />;
  };

  const getContent = () => {
    return (
      <>
        {getIcon()}
        <span
          className={clsx(classes.name, {
            [classes.linkHighlight]: isEnabledStep,
          })}
        >
          {step.name}
        </span>
        <ArrowForwardIos
          className={clsx(classes.selectIcon, {
            [classes.disabledIcon]: !isEnabledStep,
          })}
        />
      </>
    );
  };

  if (redirectTo)
    return (
      <Link
        className={clsx(classes.link, {
          [classes.disabledLink]: !isEnabledStep,
        })}
        to={redirectTo}
      >
        {getContent()}
      </Link>
    );

  return (
    <div
      className={clsx(classes.link, {
        [classes.disabledLink]: !isEnabledStep,
      })}
      onClick={() => onClickHandler(offer)}
    >
      {getContent()}
    </div>
  );
};
export default Step;
