// @flow
import _get from 'lodash/get';
import type { Offer as OfferType } from 'types/Offer';
import { FUNDING_PURCHASE_TYPES } from 'shared/Consts';
import { formatPrice } from 'helpers/number';
import _isEmpty from 'lodash/isEmpty';
import {
  displayFurniture,
  getBuyerName,
  getFullName,
} from '../../../helpers/Offer';

type fieldType = {
  label: string,
  value: string | number,
};

export const getOriginalOfferDialogFields = (
  offer: OfferType,
): Array<fieldType> => {
  if (!offer || _isEmpty(offer)) return [];

  const funding = _get(offer, 'Funding.PurchaseType', '');

  return [
    { label: 'Offer No', value: _get(offer, 'OfferNo', '') },
    {
      label: 'offer price',
      value: _get(offer, 'Offer.OfferPrice')
        ? formatPrice(_get(offer, 'Offer.OfferPrice'))
        : 'TBD',
    },
    {
      label: 'funding',
      value: funding,
    },
    {
      label: 'Loan Type',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? 'N/A'
          : _get(offer, 'Funding.LoanType', ''),
    },
    {
      label: 'finance contingency',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? 'N/A'
          : _get(offer, 'Offer.FinanceContingency', ''),
    },
    {
      label: 'due diligence contingency',
      value: _get(offer, 'Offer.InspectionContingency', ''),
    },
    {
      label: 'home sale contingency',
      value: _get(offer, 'Offer.SaleOfHomeContingency', ''),
    },
    {
      label: 'Closing Period',
      value: _get(offer, 'Offer.ClosingPeriod', ''),
    },
    {
      label: 'Furniture',
      value: displayFurniture(
        _get(offer, 'Offer.Furniture', ''),
        _get(offer, 'Offer.FurnitureOther', ''),
      ),
    },
    { label: 'Buyer Name', value: getBuyerName(offer) },
    {
      label: 'Buyer Agent Name',
      value: getFullName(_get(offer, 'AgentInfo', {})),
    },
    {
      label: 'Nile Listing Agent Name',
      value: 'Khaled Yatim',
    },
    {
      label: 'Property Address',
      value: _get(offer, 'Property.PropertyData.FullAddress', ''),
    },
  ];
};
