// @flow
import React, { Fragment } from 'react';
import type { Element } from 'react';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import Typography from '@material-ui/core/Typography';
import { Grid, makeStyles } from '@material-ui/core';
import { Dialog, DialogContent, DialogTitle } from 'components/Dialog';
import { getOriginalOfferDialogFields } from './helpers';
import { useLockBodyScroll } from '../../../hooks';

const useStyles = makeStyles(theme => ({
  spacing: {
    padding: theme.spacing(2),
  },
  title: {
    fontFamily: theme.typography.fontFamily.avenir85,
  },
  dialogContent: {
    padding: theme.spacing(2),
    paddingTop: 0,
  },
  field: {
    marginBottom: theme.spacing(1.5),
  },
  fieldLabel: {
    marginBottom: theme.spacing(1),
    color: theme.palette.primary.gray,
    textTransform: 'uppercase',
  },
  fieldValue: {
    color: theme.palette.text.secondaryDark,
  },
}));

type Props = {
  open: boolean,
  onClose: Function,
  offer: Object,
};

const OriginalOfferDialog = (
  props: Props,
): Element<typeof Dialog | typeof Fragment> => {
  const { open, onClose, offer } = props;
  const classes = useStyles();

  useLockBodyScroll({ open });

  if (_isEmpty(offer)) return <></>;

  const fields = getOriginalOfferDialogFields(offer);
  return (
    <Dialog
      open={open}
      aria-labelledby="customized-dialog-title"
      onClose={onClose}
      scroll="paper"
      fullWidth
      maxWidth="sm"
      visible={1}
    >
      <DialogTitle
        id="customized-dialog-title"
        disableTypography
        onClose={onClose}
        classes={{ root: classes.spacing }}
      >
        <Typography variant="h4" className={classes.title}>
          Offer {_get(offer, 'OfferNo', '')} - Original Offer
        </Typography>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          {fields.map(({ label, value }) => (
            <Grid item xs={4} key={label} className={classes.field}>
              <Typography variant="h6" className={classes.fieldLabel}>
                {label}
              </Typography>
              <Typography variant="h5" className={classes.fieldValue}>
                {value}
              </Typography>
            </Grid>
          ))}
        </Grid>
      </DialogContent>
    </Dialog>
  );
};

export default OriginalOfferDialog;
